.product-detalis {

	.product--tabs {

		.nav-tabs {
			display: flex;
			justify-content: space-between;
			align-items: center;

			li {

				button {
					font-size: 24px;
					line-height: 1;
					text-transform: capitalize;
					font-family: "Montserrat", sans-serif;
					margin-right: 100px;
					font-weight: 700;
					border: none;
					border-radius: 0;
					padding: 0;
					position: relative;
					line-height: 1;
					padding-bottom: 20px;
					display: block;
					color: #acacac;
					transition: all 0.3s ease;

					
					&:hover {
						color: #000000;
						background-color: transparent;
						border: none;
					}
					
				}

				 button.active {
					color: #000000;
					background-color: transparent;
					border: none;
				 }

				 button.active::before {
						width: 100% !important;
						content: "";
						position: absolute;
						bottom: 0;
						left: 0;
						right: 0;
						margin: auto;
						width: 0;
						height: 2px;
						background-color: #000000;
						transition: all 0.3s ease;
						box-sizing: border-box;
				 }
				
				 &:last-child {
					button {
						margin-right: 0 !important;
					}
				 }
			}
		}
	}
}

@media ( max-width: 992px ) {
	.product-hero.product-detalis {
		padding-bottom: 20px !important;
	}

	.product--desc {
		border-right: none !important;
	}
}

@media ( max-width: 992px ) {
	
	.product-detalis {

		.product--tabs {
	
			.nav-tabs {
				flex-direction: column;
				align-items: flex-start;

				li {

					button:before {
						display: none;
					}
				}
			}
		}
	}
}